@font-face {
  font-family: arial_geo;
  src: url("./fonts/arial_geo.ttf");
}
@font-face {
  font-family: mrglovani;
  src: url("./fonts/bpg_mrgvlovani_caps_2010.ttf");
}
@font-face {
  font-family: nino;
  src: url("./fonts/bpg_nino_mtavruli_normal.ttf");
}
.fullgame {
  width: 100%;
  height: 100vh;
  margin: auto;
  justify-content: center;
}
.game {
  /* #ea1b21 */
  width: 100%;
  height: 100vh;
  display: block;
  /* background-image: url("./img/game-background.jpg"); */
  /* background-image: url("./img/gg.png"); */
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}
.left {
  width: calc(50% - 350px);
  border: 1px solid black;
}
.result {
  width: calc(50% - 350px);
  border: 1px solid black;
}

#box {
  width: 25%;
  height: Calc(100vw * 3 / 16);
  position: absolute;
  bottom: 0;
  left: 0;
  /* background-image: url("./img/free.png"); */
  background-size: cover;
  background-position: center;
  /* background: #fff; */
  cursor: pointer;
  z-index: 99;
}

@keyframes fall {
  from {
    top: -30px;
  }
  to {
    top: Calc(100vh - 30px);
  }
}

.fallingItem {
  animation: fall 2s linear;
  animation-timing-function: linear;
  position: absolute;
  width: 10%;
  height: 18vw;
  top: -80px;
  background-image: url("./img/bottle.png");
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
}

.girl {
  width: 100%;
}

.text-center {
  color: darkred;
  font-size: 50px;
  position: absolute;
  left: 0;
  top: 0%;
  text-align: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  background-image: url("./img/game-background.jpg");
  background-size: cover;
  background-position: center;
  z-index: 9999999999;
  width: 100%;
}
.text-center form {
  width: 70%;
  margin: auto;
}
.text-center form input {
  border: none;
  border-radius: 5px;
  padding: 8px 5px 5px;
  background: rgba(255, 255, 255, 0.3);
  color: #fff;
  width: 100%;
}
.text-center form input:focus {
  outline: none;
}
.text-center form input::placeholder {
  font-family: nino;
  color: #fff;
}
.text-center form span {
  font-size: 12px;
  font-family: nino;
  display: block;
  color: #fff;
}
.life-box {
  position: absolute;
  left: 10px;
  top: 90px;
}
.life-box svg {
  margin-right: 5px;
}

/* header */
.game-header {
  width: 100%;
  display: flex;
  margin: 0;
  padding: 0;
}
.game-header li {
  list-style-type: none;
  padding: 10px 0;
}
.game-header a {
  text-align: center;
  border-right: 1px solid white;
  background: #565b61;
  width: 33.333%;
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  cursor: pointer;
  padding: 10px;
  transition: 0.5s;
  font-family: nino;
  font-size: 12px;
}
.game-header a:hover {
  color: #fff;
}
.game-header a:last-child {
  border-right: none;
}
.game-header a.active {
  background: #44484b;
}
/* ranking */

.ranking-list {
  padding: 0;
  margin: 0;
}
.ranking-list-item {
  list-style-type: none;
  background: #44484b;
  color: #fff;
  padding: 20px 0;
}
.ranking-list-item:nth-child(even) {
  background: #333b3f;
}
.ranking-box {
  display: flex;
  align-items: center;
}
.rank {
  width: 15%;
  padding-left: 20px;
}
.person {
  width: 60%;
}
.name {
  display: block;
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-family: mrglovani;
  font-size: 12px;
}
.medal {
  width: 25%;
  text-align: center;
}

/* Rules */
.rules {
  background: #44484b;
  color: #fff;
  padding-bottom: 100px;
}
.rules-header {
  padding: 15px 0 10px;
  text-align: center;
  font-family: mrglovani;
  font-size: 13px;
}
.rules-list {
  margin: 0;
  padding: 0 0 0px 30px;
}
.rules-list li {
  padding: 5px 10px;
  font-size: 9px;
  font-family: mrglovani;
  line-height: 13px;
}

/* welcome wrapper */
.welcome-wraper {
  width: 100%;
  height: 100vh;
  background-image: url("./img/wrapper-background.jpeg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;
  margin: 0;
  padding: 0;
}
.menu {
  width: 100%;
  justify-content: center;
  height: 85vh;
  display: flex;
  align-items: center;
}
.menu ul {
  margin: 0;
  padding: 0;
}
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 15vh;
  text-align: center;
}
.svg-box {
  border: 1px solid #000;
  padding: 6px 7px 2px;
  border-radius: 50%;
  display: inline-block;
  background: #fff;
}
.svg-box:nth-child(2) {
  margin-left: 10px;
}
.menu ul li {
  margin: 35px 0;
  list-style-type: none;
}
.paused-wraper {
  position: absolute;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  color: #fff;
  z-index: 99999999999;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
}

.top-nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.top-left {
  width: 50%;
  text-align: left;
  padding: 0px 20px;
}
.top-right {
  width: 50%;
  text-align: right;
  padding: 0px 5px;
}
.top-right .button,
.paused-wraper .button {
  margin-top: 3px;
  height: 39px;
  line-height: 37px;
  padding: 0 21.5px;
  font-size: 16px;
  border: 0;
  display: flex;
  margin-bottom: 0;
  margin-right: 0;
  align-items: center;
}
.paused-wraper .button {
  margin: auto;
}
.paused-wraper .button::before {
  background: none;
}

form span.your-score {
  font-size: 37px;
  color: #fff;
  font-family: mrglovani;
  word-break: break-all;
}
.occupation-button {
  /* background-image: url("./img/20.png"); */
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  padding: 25px;
  border: 1px solid #fff;
  margin-left: 5px;
}
.occupation-button:focus {
  outline: none;
}

.play-again {
  padding: 10px 15px;
  line-height: 20px;
  height: auto;
  font-size: 14px;
  display: block;
  text-shadow: 0 -1px 0 #4a8a65, 0 1px 1px #c2dece;
  border: none;
}
.play-again:before {
  line-height: 20px;
  height: auto;
}

.submit-form {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
  padding: 10px 15px;
  border: none;
  color: #fff;
  font-family: mrglovani;
}
